.card1 {
  background-color: white;
  width: 500px;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex: 1 1;
      flex: 1 1;
}

.top-form{
  margin-top: 20px;
  padding: 5px;
  -ms-flex: 1 1;
      flex: 1 1;
}

.top-form::before{
  content: 'Bill';
  text-transform: capitalize;
  font-weight: 700;
  color: hsl(184, 14%, 56%);
  margin-bottom: 1rem;

}

.top-form input{
  background: hsl(189, 41%, 97%) url(./images/icon-dollar.svg) left no-repeat;

}

.bottom-form {
  margin-bottom: 20px;
  -ms-flex: 1 1;
      flex: 1 1;
}


.bottom-form::before{
  content: 'Number of People';

  text-transform: capitalize;
  font-weight: 700;
  color: hsl(184, 14%, 56%);
  margin-bottom: 1rem;
}

.bottom-form input{
  background: hsl(189, 41%, 97%) url(./images/icon-person.svg) left no-repeat;

}

@media (min-width: 650px) {

  .card1 {
    -ms-flex-direction: row;
        flex-direction: row;
    width:600px;
    padding: 5px;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
  }
  
  .input-wrapper {
    width: 50%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    
  }
  .result-wrapper{
    padding: 10px;
    height: auto;
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
  }

}


